.recomendation{
  height: 110vh;
  background-image: url("../images/mainbanner.jpeg");
  background-attachment: fixed;
}
.recomendation-main{
  display: flex;
  flex-direction: column;
  height: 103vh;
  background-image: url("../images/about.jpeg");
  background-attachment: fixed;
  background-size: cover;
  font-family: $fontFamily;

}
.recomendation-title{
width: 100%;
 text-align: center;
  height: 10%;
  font-size: 4em;
}
.recomendation-content{
display: flex;
  width: 100%;
  height: 100%;
 }
.recomendation-content-logo{

  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.recomendation-content-text{
  width: 60%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 2em;
  cursor: pointer;

}
.recomendation-content-logo1{
 width: 100%;
  height: 20%;
 background-image: url("../images/recomendation-logos/mercedes.png");
  background-size: 70% 100%;
  background-repeat: no-repeat;



}
.recomendation-content-logo2{
  width: 100%;
  height: 20%;
  background-image: url("../images/recomendation-logos/trendglass.jpg");
  background-repeat: no-repeat;

}
.recomendation-content-logo3{
  width: 100%;
  height: 20%;
  background-image: url("../images/recomendation-logos/itmpoland.jpg");
  background-repeat: no-repeat;
  background-size: 70% 100%;

}
.recomendation-content-logo4{
  width: 100%;
  height: 20%;
  background-image: url("../images/recomendation-logos/leroymerlin.png");
  background-repeat: no-repeat;
  background-size: 70% 100%;

}
.recomendation-content-text1{
height: 20%;

}
.recomendation-content-text2{
  height: 20%;

}
.recomendation-content-text3{
  height: 20%;

}
.recomendation-content-text4{
  height: 20%;

}

.recomendation-content-text ul {
  list-style: circle;
  font-size: 0.9em;
  overflow:hidden;
  transition: all 1s;
  height: 0;

}
.recomendation-content-text1:hover ul {
  height: 100%;
}
.recomendation-content-text2:hover ul {
  height: 100%;
}
.recomendation-content-text3:hover ul {
  height: 100%;
}
.recomendation-content-text4:hover ul {
  height: 100%;
}
